import Vue from 'vue';
import Catelist from '@/components/catelist.vue'

export default {
	components:{
		Catelist
	},
	data() {
		return {
			catlist:10,
			cortyIndex:0,
			navIndex: 0,
			indexArr: {},
			hotmain: [],
			indicator: true, //是否显示指示点
			interval: 5000, //自动切换时间间隔
			duration: 400, //滑动动画时长
			autoplay: true, //是否自动切换
			circular: true, //是否采用衔接滑动
			cur: 0, //当前所在滑块的index
			page: 1,
			hasno: false //判断有没有消息
		};
	},

	// 实时获取滚动的值，到一定位置显示返回顶部
	onReachBottom(){ // 上拉加载
		this.catlist=this.catlist+10
		console.log(this.catlist)
	},
	onLoad: function(options) {
		this.load();
	},
	methods: {
		// 页面初始化
		load() {
			var that = this;
			this.$api.quest('index', {}, res => {
				console.log(res);
				that.hotmain = res.data.data.goods_list;
				that.indexArr = res.data.data;
				this.loading = true;
			});
			this.$api.quest('user/hasNoReadMessage', {}, res => {
				if (res.data.data == 1) {
					this.hasno = true;
				}
			});
		},
		gotoban() {
			uni.navigateTo({
				url: '../search/search'
			});
		},
		gotocorty(index) {
			this.cortyIndex = index;
		},
		gotoactivity(index, item) {
			this.navIndex = index;
		}
	}
};
